			<div class="container mx-auto">
				<div class="grid grid-cols-1 gap-0 text-gray-600 md:grid-cols-4 md:gap-10">
					<div>
						<h5 class="mt-10 mb-3 text-lg font-bold font-heading text-gray-900">Roll Below 33</h5>
						<p>To win you must <strong>Roll Below 33</strong>. Rolls are generated by NANO block hashes.</p>
